.c-tecnologias {
  background-color: rgba(255, 193, 0, 0.4901960784);
  position: relative;
  width: calc(100% - 2.5rem);
  z-index: 10;
}
.c-tecnologias__listado {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0.9375rem 1.5625rem;
}
.c-tecnologias__titulo {
  font-size: 1.25rem;
  font-style: italic;
  font-weight: 800;
  padding: 1rem;
}
.c-tecnologias__ul {
  padding: 0 1.5625rem;
}
.c-tecnologias__li {
  font-size: 0.9375rem;
  font-style: italic;
  position: relative;
  z-index: 10;
}
.c-tecnologias__iconos {
  display: flex;
  margin: 0 1rem;
  position: relative;
  z-index: 10;
}
.c-tecnologias__iconos-li {
  font-size: 1.5625rem;
  margin: 0.3125rem auto;
  transition: all 0.8s ease;
}
.c-tecnologias__iconos-li:hover {
  color: #ffc100;
  rotate: 360deg;
}
.c-tecnologias__separador {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
.c-tecnologias__separador div {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  z-index: 0;
}