
.c-tecnologias {
    background-color: #ffc1007d;
    position: relative;
    width: calc( 100% - 2.5rem);
    z-index: 10;
    &__listado {
        // display: grid;
        // grid-template-columns: repeat(3, 1fr);
        // column-gap: .625rem;
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding: .9375rem 1.5625rem;
    }
    &__titulo {
        font-size: 1.25rem;
        font-style: italic;
        font-weight: 800;
        padding: 1rem;
    }
    &__ul {
        padding: 0 1.5625rem;
    }
    &__li {
        font-size: .9375rem;
        font-style: italic;
        position: relative;
        z-index: 10;
    }
    &__iconos {
        display: flex;
        margin: 0 1rem;
        position: relative;
        z-index: 10;
        &-li {
            font-size: 1.5625rem;
            margin: .3125rem auto;
            transition: all .8s ease;
            &:hover {
                color: #ffc100;
                rotate: 360deg;
            }
        }
    }
    &__separador {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        div {
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            z-index: 0;
        }
    }
}